import Relation from "util/enums/Relation";
import Sex from "util/enums/Sex";
import Grade from "util/enums/Grade";
import Apply from "util/enums/Apply";
import ApplyDetail from "util/enums/ApplyDetail";
import applyDetailModel, { findNameByValue } from "models/ApplyDetailModel";
import Score from "util/enums/Score";

class Convertor {
  static Sex(sex: Sex | null) {
    let result: string;

    switch (sex) {
      case Sex.M:
        result = "";
        break;
      case Sex.W:
        result = "";
        break;
      default:
        result = "";
        break;
    }

    return result;
  }

  static Relation(parentRelation: Relation | null) {
    let result: string;

    switch (parentRelation) {
      case Relation.FATHER:
        result = "()";
        break;
      case Relation.MOTHER:
        result = "()";
        break;
      case Relation.GRANDFATHER:
        result = "()";
        break;
      case Relation.GRANDMOTHER:
        result = "()";
        break;
      case Relation.OTHER:
        result = "";
        break;
      default:
        result = "";
        break;
    }

    return result;
  }

  static GradeTypeAndGradutedDate(
    gradeType: Grade | null,
    graduatedDate: string
  ) {
    let result: string = graduatedDate;

    if (result) result += " ";

    switch (gradeType) {
      case Grade.GED:
        result += " ";
        break;
      case Grade.GRADUATED:
        result += "";
        break;
      case Grade.UNGRADUATED:
        result += " ";
        break;
      default:
        result += "";
        break;
    }

    return result;
  }

  static ApplyType(apply: Apply | null) {
    let result: string;

    switch (apply) {
      case Apply.COMMON:
        result = "";
        break;
      case Apply.SPECIAL:
        result = "";
        break;
      case Apply.OTHER:
        result = "";
        break;
      default:
        result = "";
        break;
    }

    return result;
  }

  static ApplyDetailType(apply: ApplyDetail | null) {
    const model = applyDetailModel;

    if (apply === null) return "";

    const special = model.special.find((item) => item.value === apply);

    if (special) return special.name;

    const value =
      model.special[1].models?.find((item) => item.value === apply) ||
      model.special[2].models?.find((item) => item.value === apply);

    if (value) return value.name;

    return model.other.models.find((item) => item.value === apply)?.name;
  }

  static ApplyDetailTypeList(
    apply: ApplyDetail | null,
    isIncludeCommon: boolean = false
  ) {
    if (apply === null) {
      return "";
    }
    const value = findNameByValue(apply);
    let result: string;

    if (!value) result = "";
    if (apply === ApplyDetail.COMMON) {
      if (isIncludeCommon) result = "";
      else result = "";
    } else if (
      apply === ApplyDetail.FOREIGN_EDUCATION ||
      apply === ApplyDetail.FOREIGN_SCHOOL ||
      apply === ApplyDetail.NORTH_KOREAN_DEFECTORS ||
      apply === ApplyDetail.MIDDLE_SCHOOL_EDUCATION
    ) {
      result = "";
    } else if (value !== "" && value !== "") {
      result = `\n(${value}-${this.ApplyDetailType(apply)})`;
    } else {
      result = value;
    }

    return result;
  }

  static ApplyDetailTypeInfo(apply: ApplyDetail | null) {
    if (apply === null) {
      return "";
    }
    const value = findNameByValue(apply);
    let result: string;

    if (!value) result = "";
    else if (
      apply === ApplyDetail.FOREIGN_EDUCATION ||
      apply === ApplyDetail.FOREIGN_SCHOOL ||
      apply === ApplyDetail.NORTH_KOREAN_DEFECTORS ||
      apply === ApplyDetail.MIDDLE_SCHOOL_EDUCATION
    ) {
      result = `\n(${this.ApplyDetailType(apply)})`;
    } else if (value !== "" && value !== "") {
      result = `${value}\n(${this.ApplyDetailType(apply)})`;
    } else {
      result = value;
    }

    return result;
  }

  static GradeAndFreeSem(grade: Score, freeSem: boolean) {
    let result: string;

    switch (grade) {
      case Score.A:
        result = "A/ (5)";
        break;
      case Score.B:
        result = "B/ (4)";
        break;
      case Score.C:
        result = "C/ (3)";
        break;
      case Score.D:
        result = "D/ (2)";
        break;
      case Score.E:
        result = "E/ (1)";
        break;
      default:
        result = "-";
        break;
    }

    if (freeSem) result = "-";

    return result;
  }

  static GradeType(gradeType: Grade | null) {
    let result: string;

    switch (gradeType) {
      case Grade.GRADUATED:
        result = "";
        break;
      case Grade.UNGRADUATED:
        result = "";
        break;
      case Grade.GED:
        result = "";
        break;
      default:
        result = "-";
        break;
    }

    return result;
  }
}

export default Convertor;
