import Convertor from "lib/Convertor";
import moment from "moment";
import React from "react";
import Apply from "util/enums/Apply";
import ApplyDetail from "util/enums/ApplyDetail";
import Grade from "util/enums/Grade";
import Relation from "util/enums/Relation";
import "./WritePrintParent.scss";

interface WritePrintParentProps {
  name: string;
  parentBirth: string;
  parentName: string;
  parentRelation: Relation | null;
  postCode: string;
  address: string;
  birth: string;
  applyType: Apply | null;
  applyDetailType: ApplyDetail | null;
  gradeType: Grade | null;
  schoolName: string;
  graduatedDate: string;
}

const WritePrintParent = ({
  name,
  birth,
  parentName,
  parentRelation,
  postCode,
  address,
  parentBirth,
  applyType,
  applyDetailType,
  gradeType,
  schoolName,
  graduatedDate,
}: WritePrintParentProps) => {
  return (
    <div className="print-parent">
      <div className="print-parent-title">
        {new Date().getFullYear() + 1}    
        <b> </b>
      </div>
      <table>
        <colgroup>
          <col width="70" />
          <col width="85" />
          <col width="105" />
          <col width="85" />
          <col width="85" />
          <col width="initial" />
        </colgroup>
        <tbody>
          <tr>
            <td rowSpan={3}></td>
            <td></td>
            <td>{name}</td>
            <td rowSpan={3}></td>
            <td></td>
            <td>{birth}</td>
          </tr>
          <tr>
            <td></td>
            <td>{parentName}</td>
            <td rowSpan={2}></td>
            <td rowSpan={2}>{parentBirth}</td>
          </tr>
          <tr>
            <td></td>
            <td>{Convertor.Relation(parentRelation)}</td>
          </tr>
          <tr>
            <td colSpan={2}></td>
            <td colSpan={4}>
              (: {postCode}) {address}
            </td>
          </tr>
          <tr>
            <td colSpan={2}> </td>
            <td colSpan={4}>
              {gradeType === Grade.GED
                ? `${graduatedDate}  `
                : `${schoolName} ( ${graduatedDate} ) 2 ${
                    gradeType === Grade.GRADUATED ? "" : ""
                  }`}
            </td>
          </tr>
          <tr>
            <td className="pd" rowSpan={2}>
              
              <br />
              
            </td>
            <td className="pd" colSpan={2}>
              
            </td>
            <td className="align-left" colSpan={3}>
              {Convertor.ApplyDetailTypeList(applyDetailType).includes(
                ""
              ) && ` (${Convertor.ApplyDetailType(applyDetailType)})`}
            </td>
          </tr>
          <tr>
            <td className="pd" colSpan={2}>
               
            </td>
            <td className="align-left" colSpan={3}>
              <span className="pre">
                {Convertor.ApplyDetailTypeList(applyDetailType).includes(
                  ""
                ) &&
                  ` (${Convertor.ApplyDetailType(
                    applyDetailType
                  )})\n 8  (  /   )`}
              </span>
            </td>
          </tr>
          <tr>
            <td className="lf" colSpan={6}>
              <span className="ln" />
                    ,
              <br />
                       
              
              <br />
                       
              
              <br />
              .
            </td>
          </tr>
          <tr>
            <td colSpan={6}>
              <div className="date">{moment().format("yyyy MM DD")}</div>
              <div>
                &nbsp;&nbsp;&nbsp;&nbsp; &nbsp; : {name} &nbsp;&nbsp;
                <b className="sign">()</b>
              </div>
              <div>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                : {parentName} &nbsp;&nbsp;
                <b className="sign">()</b>
              </div>
              <div className="print-footer">
                 
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      {/*         
        */}
    </div>
  );
};

export default WritePrintParent;
