import React from "react";
import WriteContent from "../../common/WriteContent";
import "./WriteIntroduction.scss";

interface WriteIntroductionProps {
  selfIntroduce: string;
  setSelfIntroduce: React.Dispatch<React.SetStateAction<string>>;
  studyPlan: string;
  setStudyPlan: React.Dispatch<React.SetStateAction<string>>;
  onSave: () => Promise<boolean>;
  isChanged: boolean;
  setIsChanged: React.Dispatch<React.SetStateAction<boolean>>;
}

const WriteIntroduction = ({
  selfIntroduce,
  setSelfIntroduce,
  studyPlan,
  setStudyPlan,
  onSave,
  isChanged,
  setIsChanged,
}: WriteIntroductionProps) => {
  return (
    <>
      <WriteContent
        title="   ()"
        onSave={onSave}
        isChanged={isChanged}
      >
        <div className="intro">
          <div className="intro-textBox">
            <p>
                      
                .
            </p>

            {/* <p>        .</p>

            <p>
                       
              .
            </p> */}
          </div>

          <label> ({selfIntroduce.length}/1500)</label>
          <textarea
            className="intro-inputBox"
            value={selfIntroduce}
            maxLength={1500}
            onChange={(e) => {
              setSelfIntroduce(e.target.value);
              setIsChanged(true);
            }}
          ></textarea>

          <label> ({studyPlan.length}/1500)</label>
          <textarea
            className="intro-inputBox"
            value={studyPlan}
            maxLength={1500}
            onChange={(e) => {
              setStudyPlan(e.target.value);
              setIsChanged(true);
            }}
          ></textarea>
        </div>
      </WriteContent>
    </>
  );
};

export default WriteIntroduction;
