import React, { useCallback, useEffect, useState } from "react";
import { observer } from "mobx-react";
import useStore from "lib/hooks/useStore";
import SecondResult from "components/ResultStatusCheck/SecondResult";
import { useHistory } from "react-router-dom";
import { handleLogin } from "lib/handleErrors";
import { FinalStatusResponse } from "util/types/Response";

interface SecondResultContainerProps {
  secondOpenModal: () => void;
  modalLoading: boolean;
  setModalLoading: React.Dispatch<React.SetStateAction<boolean>>;
}

const SecondResultContainer = ({
  secondOpenModal,
  modalLoading,
  setModalLoading,
}: SecondResultContainerProps) => {
  const history = useHistory();
  const { store } = useStore();
  const { tryGetFinalStatus } = store.StatusStore;
  const [comment, setComment] = useState<string>("");

  const {
    isPassed,
    examCode,
    name,
    sex,
    birth,
    area,
    school,
    finalApplyType,
    finalApplyDetailType,
    gradeType,
  } = store.StatusStore;

  //       useState 

  const selectComment = useCallback(
    // pass  props  comment  
    (pass?: boolean | null) => {
      if (pass) {
        setComment(". .");
      } else if (pass === false) {
        setComment(". .");
      } else if (pass === null) {
        setComment(".    .");
      }
    },
    [comment]
  );

  const getData = useCallback(() => {
    // pass  
    tryGetFinalStatus()
      .then((res) => {
        console.log("=>>", res.data);
        selectComment(res.data.isPassed);
        setModalLoading(false);
      })
      .catch((err) => {
        handleLogin(err, history);
      });
  }, [history]);

  useEffect(() => {
    getData();
  }, []);

  if (modalLoading) {
    return <></>;
  }
  return (
    <>
      {/* comment   true    */}
      {/*    comment         */}
      {/* {comment && ( */}
      <SecondResult comment={comment} secondOpenModal={secondOpenModal}>
        {examCode && (
          <div>
            <br />
            <hr />
            <br />
            <div> :{examCode} </div>
            <div> : {name}</div>
            <div> : {sex}</div>
            <div> : {birth?.split("T")[0]}</div>
            <div> : {area}</div>
            <div> : {school}</div>
            <div> : {gradeType}</div>
            {isPassed && <div>  : {finalApplyType}</div>}
            <div> : {isPassed ? "" : ""}</div>
          </div>
        )}
      </SecondResult>
      {/* )} */}
    </>
  );
};

export default observer(SecondResultContainer);
