import React, { useCallback, useEffect, useState } from "react";
import { observer } from "mobx-react";
import Category from "util/enums/Category";
import useStore from "lib/hooks/useStore";
import { PostType } from "util/types/PostType";
import { toast } from "react-toastify";
import Modal from "components/common/Modal";
import HandlePost from "components/common/HandlePost";
import { useHistory } from "react-router-dom";
import Swal from "sweetalert2";

interface HandlePostContainerProps {
  idx?: number;
  isAnswer?: boolean;
  setIsAnswer?: React.Dispatch<React.SetStateAction<boolean>>;
  category: Category;
  onClose: () => void;
  handleGetPosts: () => Promise<void>;
}

const HandlePostContainer = ({
  idx,
  isAnswer,
  setIsAnswer,
  category,
  onClose,
  handleGetPosts,
}: HandlePostContainerProps) => {
  const { store } = useStore();

  const history = useHistory();

  const { isAdmin, email } = store.AuthStore;
  const { createPost, createAnswer, getPost, deletePost, modifyPost } =
    store.BoardStore;

  const [isModify, setIsModify] = useState<boolean>(false);
  const [title, setTitle] = useState<string>("");
  const [content, setContent] = useState<string>("");
  const [post, setPost] = useState<PostType>({
    category: category,
    content: " ",
    createdAt: new Date(),
    idx: 0,
    isDeleted: false,
    parentIdx: 0,
    title: " ",
    updatedAt: new Date(),
    user: {
      idx: 0,
      isAdmin: false,
      name: " ",
      email: " ",
    },
  });

  //  
  const handleCreatePost = useCallback(async () => {
    if (!title && !content) {
      toast.warning(" .");
    } else {
      await createPost(category, title, content)
        .then((res) => {
          onClose();
          handleGetPosts();
          toast.success(".");
        })
        .catch((err) => {
          if (err.response?.status === 400) {
            toast.warning("   .");
          } else if (err.response?.status === 410) {
            toast.warning(" .");
            history.push("/login");
          } else if (err.response?.status === 401) {
            toast.warning(" .");
            history.push("/login");
          } else {
            toast.error(" .    ");
          }
        });
    }
  }, [title, content]);

  //  
  const handleCreateAnswer = useCallback(async () => {
    if (idx) {
      if (!content) {
        toast.warning(" .");
      } else {
        await createAnswer(content, idx)
          .then((res) => {
            onClose();
            handleGetPosts();
            toast.success(".");
          })
          .catch((err) => {
            if (err.response?.status === 400) {
              toast.warning("   .");
            } else if (err.response?.status === 403) {
              toast.warning(" .");
            } else if (err.response?.status === 410) {
              toast.warning(" .");
              history.push("/login");
            } else if (err.response?.status === 401) {
              toast.warning(" .");
              history.push("/login");
            } else {
              toast.error(" ");
            }
          });
      }
    }
  }, [content, isAnswer]);

  //  
  const handleModifyPost = useCallback(async () => {
    if (idx) {
      if (!title && !content) {
        toast.warning(" .");
      } else {
        await modifyPost(idx, title, content)
          .then((res) => {
            onClose();
            handleGetPosts();
            toast.success(".");
          })
          .catch((err) => {
            if (err.response?.status === 400) {
              toast.warning("   .");
            } else if (err.response?.status === 403) {
              toast.warning(" .");
            } else if (err.response?.status === 410) {
              toast.warning(" .");
              history.push("/login");
            } else if (err.response?.status === 401) {
              toast.warning(" .");
              history.push("/login");
            } else {
              toast.error(" ");
            }
          });
      }
    }
  }, [title, content, idx]);

  //  
  const handleSavePost = useCallback(() => {
    if (isAnswer) {
      handleCreateAnswer();
    } else if (isModify) {
      handleModifyPost();
    } else {
      handleCreatePost();
    }
  }, [
    handleCreatePost,
    handleModifyPost,
    handleCreateAnswer,
    isAnswer,
    isModify,
  ]);

  //  
  const handleDeletePost = useCallback(async () => {
    if (idx) {
      Swal.fire({
        title: "?",
        text: "   .",
        showCancelButton: true,
        icon: "warning",
        cancelButtonText: "",
        confirmButtonText: "",
      }).then(async (result) => {
        if (result.isConfirmed) {
          await deletePost(idx)
            .then((res) => {
              onClose();
              handleGetPosts();
              toast.success(".");
            })
            .catch((err) => {
              if (err.response?.status === 403) {
                toast.warning("    .");
              } else if (err.response?.status === 410) {
                toast.warning(" .");
                history.push("/login");
              } else if (err.response?.status === 401) {
                toast.warning(" .");
                history.push("/login");
              } else {
                toast.error(" ");
              }
            });
        }
      });
    }
  }, [idx]);

  //   
  const getPostCallback = useCallback(async () => {
    if (idx) {
      await getPost(idx)
        .then((res) => {
          setPost(res.data.post);
          setTitle(res.data.post.title);
          setContent(res.data.post.content);
        })
        .catch((err) => {
          toast.error(" .");
        });
    }
  }, [getPost, idx]);

  useEffect(() => {
    getPostCallback();
  }, [getPostCallback]);

  useEffect(() => {
    return () =>
      setPost({
        category: category,
        content: " ",
        createdAt: new Date(),
        idx: 0,
        isDeleted: false,
        parentIdx: 0,
        title: " ",
        updatedAt: new Date(),
        user: {
          idx: 0,
          isAdmin: false,
          name: " ",
          email: " ",
        },
      });
  }, []);

  return (
    <Modal onClose={onClose} className={"handle-post-modal"}>
      <HandlePost
        idx={idx}
        post={post}
        title={title}
        setTitle={setTitle}
        content={content}
        setContent={setContent}
        email={email}
        category={category}
        isModify={isModify}
        setIsModify={setIsModify}
        selectedIdx={idx}
        handleSavePost={handleSavePost}
        isAdmin={isAdmin}
        isAnswer={isAnswer}
        setIsAnswer={setIsAnswer}
        handleDeletePost={handleDeletePost}
      />
    </Modal>
  );
};

export default observer(HandlePostContainer);
