enum Apply {
  //  
  COMMON = "COMMON",
  //  
  SPECIAL = "SPECIAL",
  //  
  OTHER = "OTHER",
}

export function findApplyByString(apply: Apply) {
  if (apply === Apply.COMMON) return " ";
  if (apply === Apply.SPECIAL) return "  ";
  if (apply === Apply.OTHER) return " ";
}
export default Apply;
